.work-description {
  background-color: #ffffff;
}

.work-description__wrapper {
  .container();
}

.work-description__title {
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 40px;

  @media (min-width: @tablet-width) {
    font-size: 60px;
    line-height: 60px;
  }

  @media (min-width: @desktop-width) {

  }
}

.work-description__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  counter-reset: num;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 77px 137px;
    margin-bottom: 47px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 78px;
  }
}

.work-description__item {
  display: flex;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    position: relative;
  }
}

.work-description__item::after {
  @media (min-width: @tablet-width) {
    content: counter(num);
    counter-increment: num;
    color: #f2f2f2;
    position: absolute;
    font-size: 280px;
    line-height: 280px;
    z-index: 0;
    top: -40px;
    right: 35px;
  }

  @media (min-width: @desktop-width) {
    right: 0;
  }
}
